<template>
<div class="main-page">
  <div class="headerTag">工作报告</div>
  <form class="h-form">
    <item must name="寄存烟数量">
      <input v-model="info.FNumber" />
      <span style="font-size: 13px;width: 40px;display: block;margin-left:10px;color:#666">户数</span>
    </item>
    <item must name="破损烟数量" class="text-one">
      <input v-model="info.FNum" />
      <span style="font-size: 13px;width: 40px;display: block;margin-left:10px;color:#666">户数</span>
    </item>
    <item must name="汇报日期">
      <input v-model="info.FTime" readonly @click="chooseTime('FTime')" />
    </item>
    <item name="备注" class="text-one">
      <textarea v-model="info.FContent" placeholder="请输入送货员、驾驶员、路线号"/>
      </item>
    </form>
    <van-button
      type="primary"
      block
      size="normal"
      :color="$config.theme"
      style="width: calc(100% - 30px); margin: 3px 15px"
      @click="submit"
      :loading="loading"
      >提交</van-button
    >
    <van-popup v-model="timeShow" position="bottom">
      <van-datetime-picker
        type="date"
        title="选择日期"
        @confirm="confirmTime"
      />
    </van-popup>
    <van-popup v-model="pickerShow" position="bottom">
      <van-picker
        show-toolbar
        :columns="columns"
        @confirm="confirmPicker"
        @cancel="pickerShow = false"
      />
    </van-popup>
  </div>
</template>

<script>
export default {
  name: "EventAE",
  props: {},
  components: {},
  data() {
    return {
      apis: {
        edit: this._api.OA_WorkReportEdit,
        create: this._api.OA_WorkReportCreate,
        list: this._api.OA_WorkReportManage,
        delete: this._api.OA_WorkReportDelete,
        audit: this._api.OA_WorkReportAudit,
        details: this._api.OA_WorkReportDetails,
      },
      loading: false,
      curId: null,
      info: {
        PersonName: "",
        FName: "",
        EventList: [],
      },
      rules: {
        FNumber: "请输入寄存烟数量！",
        FNum: "请输入破损烟数量！",
        FTime: "请选择汇报时间！"
      },
      curTimeFieldName: "",
      timeShow: false,
      pickerShow: false,
      classList: [],
      columns: [],
    };
  },
  methods: {
    async submit() {
      await this.$u.checkForm(this.info, this.rules);
      this.loading = true;
      let apiFun = this.curId ? this.apis.edit : this.apis.create;
      apiFun(this.info).then((res) => {
        if (res) {
          this.$u.msg(this.info.Id ? "编辑成功！" : "创建成功！");
          setTimeout(() => {
            this.$router.back();
          }, 200);
        }
        this.loading = false;
      });
    },
    chooseTime(fieldName) {
      this.curTimeFieldName = fieldName;
      this.timeShow = true;
    },
    confirmTime(time) {
      this.timeShow = false;
      this.info[this.curTimeFieldName] = this.$time(time).format("YYYY-MM-DD");
    },
    choosePerson() {
      window.temFun = (person) => {
        this.$set(this.info, "PersonName", person.Fname);
        this.$set(this.info, "PersonId", person.Id);
      };
      this.$router.push(`/modular/oa/ChoosePerson`);
    },
    choosePicker() {
      this.pickerShow = true;
    },
    chooseCustomer() {
      window.temFun = (item) => {
        this.$set(this.info, "CustomerName", item.FCustName);
        this.$set(this.info, "CustomerId", item.Id);
      };
      this.$router.push("/modular/oa/CustomerChoose");
    },
    confirmPicker(value, index) {
      this.pickerShow = false;
      this.info.ClassName = value;
      this.info.ClassId = this.classList[index].Id;
    },
    OA_Event_ClassIndex() {
      this._api.OA_Event_ClassIndex().then((res) => {
        if (res) {
          this.classList = res.data;
          this.columns = res.data.map((it) => it.ClassName);
        }
      });
    },
    chooseMaterial() {
      window.temFun = (material) => {
        this.info.EventList.push({
          FType: null,
          FQty: null,
          MaterialName: material.FName,
          MaterialId: material.Id,
        });
      };
      this.$router.push(`/modular/oa/MaterialChoose`);
    },
  },
  mounted() {
    this.info = {
      EventList: [],
    };
    this.curId = this.$route.query.Id;
    this.OA_Event_ClassIndex();
  },
};
</script>

<style lang="scss">
.car-item {
  width: calc(100% - 30px);
  margin-left: 15px;
  margin-top: 15px;
  padding: 0 15px;
  font-size: 14px;

  .car-item-line {
    line-height: 30px;
    margin-right: 15px;
  }
}

.van-radio-group {
  display: flex;
}
textarea{
	width: 100%;
	height: 100px;
}
</style>
